.container {
    width: 100%;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
    max-width: 320px;
    @media screen and (min-width: 768px) {
      max-width: 768px;
      padding-left: 32px;
      padding-right: 32px;
    }
    @media screen and (min-width: 1024px) {
      max-width: 1024px;
    }
  }